@import url(https://fonts.googleapis.com/css?family=Sora:400,600&display=swap);
.home {
  width: 100%;
  height: 100vh;
}
.home h1 {
  font-size: 72px;
  text-shadow: 10px 10px 5px rgba(255,255,255,0.5);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.parallax {
  overflow: hidden;
  height: 80vh;
  background-image: url(/Animations/static/media/4.11392458.png), url(/Animations/static/media/3.a415df76.png), url(/Animations/static/media/2.52aa40d3.png), url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  transition: 0.2s ease;
}
.anim3 {
  padding: 20px;
}
.anim3 p {
  width: 75%;
  margin: 0 auto;
  line-height: 2;
  text-align: center;
}
.anim3 p:first-of-type {
  margin-bottom: 20px;
}
.anim3 p:first-of-type span {
  color: var(--details);
}
.anim3 span {
  position: relative;
  top: 2em;
  opacity: 0;
  color: var(--neon);
  animation: text 1s ease-in-out forwards;
}
/* .anim3 span:nth-child(4n+1) {
  animation-delay: 0s;
}
.anim3 span:nth-child(4n+2) {
  animation-delay: 0.5s;
}
.anim3 span:nth-child(4n+3) {
  animation-delay: 1s;
}
.anim3 span:nth-child(4n) {
  animation-delay: 1.5s;
} */
.anim3 span:nth-child(8n+1) {
  animation-delay: 0s;
}
.anim3 span:nth-child(8n+2) {
  animation-delay: .3s;
}
.anim3 span:nth-child(8n+3) {
  animation-delay: .6s;
}
.anim3 span:nth-child(8n+4) {
  animation-delay: .9s;
}
.anim3 span:nth-child(8n+5) {
  animation-delay: 1.2s;
}
.anim3 span:nth-child(8n+6) {
  animation-delay: 1.5s;
}
.anim3 span:nth-child(8n+7) {
  animation-delay: 1.8s;
}
.anim3 span:nth-child(8n) {
  animation-delay: 2.1s;
}
@keyframes text {
  100% {opacity: 1; top: 0; color: var(--main_light);}
}
.loaders_box {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.loaders h1 {
  text-align: center;
  margin-bottom: 20px;
}
.loader {
  width: 20vw;
  height: 20vw;
  position: relative;
}
/* loader1 */
.loader1 div {
  position: absolute;
  border-radius: 50%;
  border: 2vw solid var(--details2);
  border-top-color: var(--neon);
  animation: spin 2s infinite;
}
.loader1 div:first-of-type {
  width: 20vw;
  height: 20vw;
}
.loader1 div:nth-of-type(2) {
  width: 13.333vw;
  height: 13.333vw;
  animation-delay: .25s;
}
.loader1 div:last-of-type {
  width: 6.667vw;
  height: 6.667vw;
  animation-delay: .5s;
}

/* loader2 */
.loader2 div {
  width: 15vw;
  height: 15vw;
  position: relative;
  margin: 1vw auto;
  border-radius: 50%;
  border: 3px solid transparent;
  border-left: 3px solid var(--neon);
  border-right: 3px solid var(--neon);
  background: radial-gradient(var(--details) 5%, transparent 25%);
  animation: spin_right 2s linear infinite;
}
.loader2 div::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 8vw;
  height: 8vw;
  border: 2px solid transparent;
  border-left: 2px solid var(--neon);
  border-right: 2px solid var(--neon);
  border-radius: 50%;
  animation: spin_left 1s linear infinite;
}

/* loader3 */
.loader3::before, .loader3::after {
  content: "";
  position: absolute;
  border-radius: 50%;
  transform-origin: center;
}
.loader3::after {
  box-shadow: inset 25px 0 0 var(--details2), inset -25px 0 0 var(--details);
  animation: morpho1 2s linear infinite;
  width: 18vw;
  height: 18vw;
}
.loader3::before {
  box-shadow: inset 25px 0 0 var(--neon), inset -25px 0 0 var(--neon2);
  animation: morpho2 2s linear infinite;
  width: 18vw;
  height: 18vw;
}

@keyframes spin {
  50%,
  100% {
    transform: translate(-50%,-50%) rotate(360deg);
  }
}

@keyframes spin_right {
  to {transform: rotate(360deg);}
}
@keyframes spin_left {
  to {transform: translate(-50%,-50%) rotate(-360deg);}
}

@keyframes morpho1 {
  0% {transform: rotateZ(0deg) scaleX(1) scaleY(1);}
  50% {transform: rotateZ(180deg) scaleX(0.45) scaleY(0.95);}
  100% {transform: rotateZ(360deg) scaleX(1) scaleY(1);}
}
@keyframes morpho2 {
  0% {transform: rotateZ(0deg) scaleX(1) scaleY(1);}
  50% {transform: rotateZ(-180deg) scaleX(0.95) scaleY(0.45);}
  100% {transform: rotateZ(-360deg) scaleX(1) scaleY(1);}
}
.Anim5 {
  height: 80vh;
}
.Anim5 .bg {
  width: 100%;
  height: 100%;
  opacity: 0;
  background: linear-gradient(110deg, transparent 40%, var(--details2), var(--details), var(--details2), transparent 60%), linear-gradient(80deg, transparent 40%, var(--details2), var(--details), var(--details2), transparent 60%);
  animation: show 5s linear forwards;
}
.Anim5 .box {
  width: 50%;
  position: absolute;
  transform: translate(-50%,-50%);
}
.Anim5 .hand1_box {
  top: 45%;
  left: 28%;
}
.Anim5 .hand2_box {
  top: 54%;
  left: 77%;
}
.Anim5 path {
  fill: transparent;
  stroke-width: 0.5px;
  animation: draw 5s ease-in-out forwards;
}
.Anim5 .hand1 {
  stroke-dasharray: 325;
  stroke-dashoffset: 325;
  stroke: var(--details);
}
.Anim5 .hand2 {
  stroke-dasharray: 305;
  stroke-dashoffset: 305;
  stroke: var(--neon);
}

@keyframes draw {
  to {stroke-dashoffset: 0; stroke: var(--main_light);}
}

@keyframes show {
  to {opacity: 1;}
}
.Anim6 {
  height: 80vh;
}
.Anim6 svg {
  height: 80vh;
  position: absolute;
}
.Anim6 path {
  fill-opacity: 0.2;
  fill: var(--neon2);
  stroke-width: 0.5px;
  stroke: var(--details2);
  animation: draw2 5s ease-in-out forwards;
}
.Anim6 .state1 {
  fill-opacity: 0;
  stroke-dasharray: 480;
  stroke-dashoffset: 480;
}
.Anim6 .state2 {
  stroke-dasharray: 285;
  stroke-dashoffset: 285;
}
.Anim6 .state3 {
  stroke-dasharray: 205;
  stroke-dashoffset: 205;
}
.Anim6 .state4 {
  stroke-dasharray: 170;
  stroke-dashoffset: 170;
}
.Anim6 .state5 {
  stroke-dasharray: 100;
  stroke-dashoffset: 100;
}

@keyframes draw2 {
  30% {stroke-width: 2px;}
  70% {stroke-width: 0.5px;}
  100% {stroke-dashoffset: 0; fill: transparent; stroke: var(--main_light);}
}

/* --General-- */
:root {
  --main_font: 'Sora', Verdana, sans-serif;
  --main_dark: rgb(40,40,43);
  --main_light: rgb(255,255,255);
  --shadow: rgb(206,200,205);
  --neon: rgb(255,170,33);
  --neon2: rgba(255,170,33,0.3);
  --details: rgb(94,45,150);
  --details2: rgba(94,45,150,0.3);
}
* {
	box-sizing: border-box;
	-webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-tap-highlight-color: transparent;
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Sora', Verdana, sans-serif;
  font-family: var(--main_font);
  color: rgb(255,255,255);
  color: var(--main_light);
  height: 100vh;
  background-color: rgb(40,40,43);
  background-color: var(--main_dark);
  min-width: 350px;
}
a {
  text-decoration: none;
  color: rgb(255,255,255);
  color: var(--main_light);
  font-family: 'Sora', Verdana, sans-serif;
  font-family: var(--main_font);
}
ul {
  list-style: none;
}
.app {
  padding: 1%;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
.home_icon {
  max-width: 60px;
  height: auto;
  opacity: 0.5;
  transition: 0.4s ease;
}
.home_icon:hover {
  opacity: 1;
}
.container {
  position: absolute;
  width: 80%;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.center {
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
footer {
  position: absolute;
  bottom: 1%;
  font-size: 0.9rem;
}
